"use server";

import { db } from "@/lib/prisma";
import { auth } from "@clerk/nextjs/server";
import { GoogleGenerativeAI } from "@google/generative-ai";

const API_KEY = process.env.GEMINI_API_KEY;
if (!API_KEY) {
  console.warn("GEMINI_API_KEY is not set. generateQuiz and improvement tip generation will fail without it.");
}

const genAI = new GoogleGenerativeAI(API_KEY);
const model = genAI.getGenerativeModel({ model: "gemini-1.5-flash" });

// Extra preset questions (5) that will be appended to every quiz
const EXTRA_QUESTIONS = [
  {
    question: "What is the time complexity of accessing an element in an array by index?",
    options: ["O(1)", "O(log n)", "O(n)", "O(n log n)"],
    correctAnswer: "O(1)",
    explanation: "Access by index in an array is constant time."
  },
  {
    question: "Which data structure is best suited for BFS traversal of a graph?",
    options: ["Stack", "Queue", "Priority Queue", "HashMap"],
    correctAnswer: "Queue",
    explanation: "BFS uses a queue to traverse nodes level by level."
  },
  {
    question: "Which sorting algorithm is stable by default?",
    options: ["QuickSort", "MergeSort", "HeapSort", "SelectionSort"],
    correctAnswer: "MergeSort",
    explanation: "MergeSort preserves the relative order of equal elements, making it stable."
  },
  {
    question: "In SQL, which clause is used to remove duplicate rows from the result set?",
    options: ["GROUP BY", "DISTINCT", "UNIQUE", "HAVING"],
    correctAnswer: "DISTINCT",
    explanation: "DISTINCT removes duplicate rows from query results."
  },
  {
    question: "What does ACID stand for in databases?",
    options: ["Atomicity, Consistency, Isolation, Durability", "Availability, Consistency, Integrity, Durability", "Atomicity, Concurrency, Isolation, Durability", "Authentication, Consistency, Isolation, Durability"],
    correctAnswer: "Atomicity, Consistency, Isolation, Durability",
    explanation: "ACID properties ensure reliable transactions in databases."
  }
];

export async function generateQuiz(count = 10) {
  const { userId } = await auth();
  if (!userId) throw new Error("Unauthorized");

  const requested = Number(count) || 10;
  const questionCount = Math.min(Math.max(requested, 1), 50);

  // If API key missing, return fallback immediately (plus extra questions)
  if (!API_KEY) {
    console.warn("GEMINI_API_KEY missing — returning fallback questions");
    const fallback = Array.from({ length: questionCount }, (_, i) => ({
      question: `Fallback Q${i + 1}: What is a common data structure for LIFO?`,
      options: ["Queue", "Stack", "Tree", "Graph"],
      correctAnswer: "Stack",
      explanation: "A stack follows Last-In-First-Out (LIFO).",
    }));
    return [...fallback, ...EXTRA_QUESTIONS];
  }

  const user = await db.user.findUnique({
    where: { clerkUserId: userId },
    select: {
      industry: true,
      skills: true,
    },
  });

  if (!user) throw new Error("User not found");

  const prompt = `
    Generate ${questionCount} technical interview questions for a ${
    user.industry
  } professional${
    user.skills?.length ? ` with expertise in ${user.skills.join(", ")}` : ""
  }.
    
    Each question should be multiple choice with 4 options.
    
    Return the response in this JSON format only, no additional text:
    {
      "questions": [
        {
          "question": "string",
          "options": ["string", "string", "string", "string"],
          "correctAnswer": "string",
          "explanation": "string"
        }
      ]
    }
  `;

  try {
    const result = await model.generateContent(prompt);
    const rawText = await result.response.text();
    const cleanedText = rawText.replace(/```(?:json)?\n?/g, "").trim();

    let quiz;
    try {
      quiz = JSON.parse(cleanedText);
    } catch (parseErr) {
      const match = cleanedText.match(/\{[\s\S]*\}/);
      if (match) {
        try {
          quiz = JSON.parse(match[0]);
        } catch (e2) {
          console.error("Failed to parse JSON from AI response after extracting object:", e2, "cleanedText:", cleanedText);
          throw new Error("Failed to parse AI response as JSON");
        }
      } else {
        console.error("No JSON found in AI response:", cleanedText);
        throw new Error("No JSON found in AI response");
      }
    }

    const questions = Array.isArray(quiz.questions) ? quiz.questions.slice(0, questionCount) : [];

    // pad with fallback questions if model returned fewer than requested
    let finalQuestions = questions;
    if (questions.length < questionCount) {
      const padding = Array.from({ length: questionCount - questions.length }, (_, i) => ({
        question: `Fallback Q${i + 1}: What is a common data structure for LIFO?`,
        options: ["Queue", "Stack", "Tree", "Graph"],
        correctAnswer: "Stack",
        explanation: "A stack follows Last-In-First-Out (LIFO).",
      }));
      finalQuestions = [...questions, ...padding];
    }

    // append the 5 extra preset questions
    finalQuestions = [...finalQuestions, ...EXTRA_QUESTIONS];

    return finalQuestions;
  } catch (error) {
    console.error("Error generating quiz:", error);
    // return fallback so UI doesn't break, plus extra questions
    const fallback = Array.from({ length: questionCount }, (_, i) => ({
      question: `Fallback Q${i + 1}: What is a common data structure for LIFO?`,
      options: ["Queue", "Stack", "Tree", "Graph"],
      correctAnswer: "Stack",
      explanation: "A stack follows Last-In-First-Out (LIFO).",
    }));
    return [...fallback, ...EXTRA_QUESTIONS];
  }
}

export async function saveQuizResult(questions, answers, score) {
  const { userId } = await auth();
  if (!userId) throw new Error("Unauthorized");

  const user = await db.user.findUnique({
    where: { clerkUserId: userId },
  });

  if (!user) throw new Error("User not found");

  const questionResults = questions.map((q, index) => ({
    question: q.question,
    answer: q.correctAnswer,
    userAnswer: answers[index],
    isCorrect: q.correctAnswer === answers[index],
    explanation: q.explanation,
  }));

  const wrongAnswers = questionResults.filter((q) => !q.isCorrect);

  let improvementTip = null;
  if (wrongAnswers.length > 0 && API_KEY) {
    const wrongQuestionsText = wrongAnswers
      .map(
        (q) =>
          `Question: "${q.question}"\nCorrect Answer: "${q.answer}"\nUser Answer: "${q.userAnswer}"`
      )
      .join("\n\n");

    const improvementPrompt = `
      The user got the following ${user.industry} technical interview questions wrong:

      ${wrongQuestionsText}

      Based on these mistakes, provide a concise, specific improvement tip.
      Focus on the knowledge gaps revealed by these wrong answers.
      Keep the response under 2 sentences and make it encouraging.
      Don't explicitly mention the mistakes, instead focus on what to learn/practice.
    `;

    try {
      const tipResult = await model.generateContent(improvementPrompt);
      const tipRaw = await tipResult.response.text();
      const tipClean = tipRaw.replace(/```(?:json)?\n?/g, "").trim();
      console.debug("improvement tip raw:", tipRaw);
      console.debug("improvement tip cleaned:", tipClean);
      improvementTip = tipClean;
    } catch (error) {
      console.error("Error generating improvement tip:", error);
    }
  }

  try {
    const assessment = await db.assessment.create({
      data: {
        userId: user.id,
        quizScore: score,
        questions: questionResults,
        category: "Technical",
        improvementTip,
      },
    });

    return assessment;
  } catch (error) {
    console.error("Error saving quiz result:", error);
    throw new Error("Failed to save quiz result");
  }
}

export async function getAssessments() {
  const { userId } = await auth();
  if (!userId) throw new Error("Unauthorized");

  const user = await db.user.findUnique({
    where: { clerkUserId: userId },
  });

  if (!user) throw new Error("User not found");

  try {
    const assessments = await db.assessment.findMany({
      where: {
        userId: user.id,
      },
      orderBy: {
        createdAt: "asc",
      },
    });

    return assessments;
  } catch (error) {
    console.error("Error fetching assessments:", error);
    throw new Error("Failed to fetch assessments");
  }
}
